<template>
	<view class="container mms-page">
		<view class="section">
			<view class="section-info">
				<view>物料名称：<view class="list-item-info">{{materialsData.materialsName}}</view></view>
				<view>物料编号：<view class="list-item-info">{{materialsData.materialsNo}}</view></view>
				<view>规格型号：<view class="list-item-info">{{materialsData.materialsModel}}</view></view>
				<view>库存数量：<view class="list-item-info">{{materialsData.inStockNum}} {{materialsData.materialsUnit}}</view></view>
			    
			</view>
		</view>
		<view class="mms-content">
			<uni-segmented-control :current="segmCurrent" :values="segmItems" @clickItem="onClickItem" styleType="button" activeColor="#6d3b85"></uni-segmented-control>
			        <view class="content">
			            <view v-show="segmCurrent === 0">
			                <view class="mms-list">
			                	<!-- <view class="section-title">
			                		<view class="section-title-text">入库登记</view>
			                		<view class="title-right-txt">查询结果：{{inOrderLength}} 条记录</view>
			                	</view> -->
			                	<!-- <view class="list"> -->
			                		<view class="list-item"  v-for="item in inOrderList" :key="item.code">
			                			<!-- <view class="list-item-title">
			                				<view class="list-title-icon">
			                					<uni-icons custom-prefix="iconfont" color="#6d3b85" type="icon-xiantiao-1" size="24"></uni-icons> 
			                				</view>
			                			   {{item.type}}
			                			   <view class="item-time">{{item.createTime}}</view>
			                			</view> -->
			                			<view>当前库存：<view class="list-item-info">{{item.currentInStockNum}} {{item.materialsUnit}}</view></view>
			                			<!-- <view v-if="item.type == '入库'">入库数量：<view class="list-item-info">{{item.num2}} 个</view></view> -->
			                			<view>入库数量：<view class="list-item-info">{{item.wareHousingNum}} {{item.materialsUnit}}</view></view>
			                			<view>存放位置：<view class="list-item-info">{{item.storehouseName}}</view></view>
			                			<view>登记人员：<view class="list-item-info">{{item.operator}} </view></view>
			                			<view>所属部门：<view class="list-item-info">{{item.dept}}</view></view>
			                			<view>备注：<view class="list-item-info">{{item.remark}}</view></view>
			                			<view>入库日期：<view class="list-item-info">{{item.wareHousingTime}}</view></view>
			                			<view>创建时间：<view class="list-item-info">{{item.createTime}}</view></view>
			                		</view>
			                	<!-- </view> -->
			                </view>
			            </view>
			            <view v-show="segmCurrent === 1">
			                <view class="mms-list">
			                		<view class="list-item"  v-for="item in outOrderList" :key="item.code">
			                			<view>当前库存：<view class="list-item-info">{{item.currentInStockNum}} {{item.materialsUnit}}</view></view>
			                			<view>出库数量：<view class="list-item-info">{{item.outOfStockNum}} {{item.materialsUnit}}</view></view>
			                			<view>存放位置：<view class="list-item-info">{{item.storehouseName}}</view></view>
			                			<view>登记人员：<view class="list-item-info">{{item.operator}} </view></view>
			                			<view>所属部门：<view class="list-item-info">{{item.dept}}</view></view>
			                			<view>备注：<view class="list-item-info">{{item.remark}}</view></view>
					                    <view>出库日期：<view class="list-item-info">{{item.outOfStockTime}}</view></view>
			                			<view>创建时间：<view class="list-item-info">{{item.createTime}}</view></view>
			                		</view>
			                </view>
			            </view>
			        </view>
		</view>
	</view>
</template>

<script>
	const util = require('../../static/js/util.js');
	export default {
		data() {
			return {
				userState: {},
				searchData:{}, 
				segmCurrent:0,
				segmItems:['入库记录','出库记录'],
				materialsData:{},
				inOrderList:[],
				inOrderLength:0,
				outOrderList:[],
				outOrderLength:0,
			}
		},
		onLoad:function(option){
			const materialsNo = option.code;
			this.userState = util.xcStorage.getStorageSync('userState');
			this.getMaterials(materialsNo);
			this.getInOrder(materialsNo);
			this.getOutOrder(materialsNo); 
		},
		created(){},
		methods: { 
			onClickItem(e){
				this.segmCurrent = e.currentIndex;
			},
			getMaterials(code){
				const _this = this;
				const url = util.xcRequestURL.WAREHOUSE.getMaterialList + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url,{materialsNo:code}, function(res) {
					// const { result } = res;
					if(res.status == 200){
						let resList = res.result;
						if(resList.length > 0){ 
						  _this.materialsData = res.result[0];
						} 
					}
				});
			},
			getInOrder(code){
				const _this = this;
				const url = util.xcRequestURL.WAREHOUSE.getInHouseRecordList + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url,{materialsNo:code}, function(res) {
					if(res.status == 200){ 
						let resList = res.result;
						if(resList.length > 0){
							_this.inOrderList = res.result;
						}
					}
				});
			},
			getOutOrder(code){
				const _this = this;
				const url = util.xcRequestURL.WAREHOUSE.getOutHouseRecordList + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url,{materialsNo:code}, function(res) {
					if(res.status == 200){ 
						let resList = res.result;
						if(resList.length > 0){
							_this.outOrderList = res.result;
						}
					}
				});
			},
			onTimeChange(e){
				this.wareHousingTime = e;
				this.formData.wareHousingTime = e;
			}
		}
	}
</script>

<style>
	
	.container {
		/* padding: 20px; */
		font-size: 14px;
		line-height: 24px;
		box-sizing: border-box;
	}
	
	.section{
		padding:10px;
		background:#fff;
	}
	.section-info{
		font-size:13px;
		color:#888;
	}
	.section-title{
		position:relative;
		background:#fff;
		margin:0 0 12px;
		margin-bottom:6px;
		padding:6px 12px;
	}
	.section-title-text{
		padding-left:15px;
		border-left:3px solid #FFB800;
	}
	.title-right-txt{
		position:absolute;
		top:5px;
		right:20px;
		font-size:12px;
		color:#999;
	}
	.mms-content{
		padding:15px;
	}
	.mms-list{
		/* margin-top:15px; */
		padding:12px 0 12px;
		height:calc(100vh - 210px);
		overflow-y:auto;
	}
	.list-item-title{
		padding:0 0 5px 12px;
		margin-bottom:5px;
		font-size:14px;
		color:#444;
		box-sizing: border-box;
		border-bottom:1px solid #efefef;
	}
	.item-time{
		display:inline-block;
		float:right;
		font-size:12px;
		color:#888;
	}
	.list-title-icon{
		display:inline-block;
		position:absolute;
		left:15px;
		top:15px;
	}
	.list-item{
		position:relative;
		margin-bottom:10px;
		padding:12px;
		font-size:13px;
		line-height:1.8;
		color:#888;
		background:#fff;
		border-radius:8px;
	}
	.list-item-info{
		display:inline-block;
		width:calc(100% - 90px);
		vertical-align: top;
		word-break:break-all;
		overflow:hidden;
		color:#444;
	}
	
</style>
